import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import controlCSS from './control.module.css'
import IconButton from '@material-ui/core/IconButton';
import Slider from '@material-ui/core/Slider';
import { FormatItalic, FormatBold, FormatUnderlined, CloudDownload } from '@material-ui/icons';
import { fabric } from "fabric";
import { FormControl, Select, MenuItem, InputLabel, Drawer, Checkbox, FormControlLabel, RadioGroup, Radio, FormLabel, Typography } from '@material-ui/core';


const deft = { fill: "#000000", fontStyle: "normal", fontWeight: "normal", underline: false, fontFamily: "Times New Roman", opacity: 1} //default watermark config
const deftR = { x: false, y: false } //default repeat pattern config
class Control extends Component {
    constructor() {
        super();
        this.state = {
            input: "",
            watermark: {},
            watermarkProps: deft,
            drawer: false,
            outputProps: { numTag: false, format: "png", filename: "default" },
            repeat: deftR
        }
        this.handleText = this.handleText.bind(this);   //bind functions to this in order to access props
        this.delCanvas = this.delCanvas.bind(this);
        this.handleInput = this.handleInput.bind(this);
        this.handleTextChange = this.handleTextChange.bind(this);
        this.handleFontFamily = this.handleFontFamily.bind(this);
        this.handleOpacity = this.handleOpacity.bind(this);
        this.convertToImagen = this.convertToImagen.bind(this);
        this.batchDownload = this.batchDownload.bind(this);

    }

    handleInput(value) { //save text input to state
        this.setState({ input: value });
    }

    handleOutputProps(e) {  //save values from download menu
        let value = e.currentTarget.value
        let name = e.currentTarget.name
        if (name === "format" || name === "filename") {
            this.setState(prevState => ({ outputProps: { ...prevState.outputProps, [name]: value } }), () => console.log(this.state.outputProps))
        } else if (name === "numTag") {
            this.setState(prevState => ({ outputProps: { ...prevState.outputProps, [name]: !prevState.outputProps[name] } }), () => console.log(this.state.outputProps))
        }
    }

    handleRepeat(e) {  //apply repeat pattern 
        let name = e.currentTarget.name
        let watermark = this.state.watermark;
        if (watermark instanceof fabric.Text) { //type check
            let canvas = this.props.canvas;
            this.setState(prevState => ({ repeat: { ...prevState.repeat, [name]: !prevState.repeat[name] } }), () => {
                if (this.state.repeat[name]) {
                    let canvasMark = canvas.item(1); //get watermark
                    watermark.clone((cloned) => {  //clone watermark  
                        let group = new fabric.Group([], {  //create a group
                            globalCompositeOperation: 'source-atop'
                        });
                        let coords = cloned.getCoords(); //get coordinate
                        let offset
                        if (name === "y") {  //get the y of 2 points
                            let tr = coords[1].y;
                            let bl = coords[3].y;
                            offset = Math.abs(tr - bl); //the length is offset
                        }
                        if (name === "x") {  //get the x of 2 points
                            let tr = coords[1].x;
                            let bl = coords[3].x;
                            offset = Math.abs(tr - bl); //the length is offset
                        }
                        canvas.add(group);
                        cloned.set({ globalCompositeOperation: 'source-over' })
                        group.add(cloned);
                        group.addWithUpdate();
                        canvas.discardActiveObject();
                        canvas.setActiveObject(group);
                        console.log(canvas.getObjects())
                        this.paste(group, cloned, offset, name); //keep pasting until fill the image  
                        canvasMark.set({  //set single watermark object invisible
                            visible: false
                        })
                    });
                }
                if (this.state.repeat[name] === false) {
                    if (canvas.item(2) instanceof fabric.Group) {
                        canvas.remove(canvas.item(2))
                        canvas.item(1).set({
                            visible: true
                        })
                        canvas.requestRenderAll()
                    }
                }
            })
        }
    }

    paste(group, clone, offset, name) { //implement paste
        let canvas = this.props.canvas
        let image = canvas.getObjects()[0]
        clone.clone((cloned) => { //keep cloning the watermark
            if (name === "y") {
                cloned.set({
                    left: cloned.left,
                    top: cloned.top + offset,
                });
            }
            if (name === "x") {
                cloned.set({
                    left: cloned.left + offset + 10,
                    top: cloned.top,
                });
            }
            group.add(cloned)
            group.addWithUpdate() //recalculate the group to avoid bugs
            console.log(group.height);
            console.log(image.height);
            if (name === "y") {
                if (group.height > image.height) { //see the pattern if longer than image, if yes stop cloning  
                    canvas.requestRenderAll();
                    return;
                }
            }
            if (name === "x") {
                if (group.width > image.width) { //see the pattern if wider than image, if yes stop cloning 
                    canvas.requestRenderAll();
                    return;
                }
            }
            group.setCoords();
            this.paste(group, cloned, offset, name); //else keep pasting
        })
    }


    handleProps(e) { //general function for watermark props
        let value = e.currentTarget.value //get value from click location 
        //console.log(value);
        let name = e.currentTarget.name
        if (name === "fontWeight" || name === "fontStyle") { //handle bold and italic
            if (this.state.watermarkProps[name] === value) { //if selected 
                this.setState(preState => (
                    {
                        watermarkProps: { ...preState.watermarkProps, [name]: "normal" }  //set to default
                    }), () => this.handleTextChange(name))
            } else {
                this.setState(preState => ( //if default
                    {
                        watermarkProps: { ...preState.watermarkProps, [name]: value } //set to the value
                    }), () => this.handleTextChange(name))
            }
        } else if (name === "underline") {  //for underline boolean 
            this.setState(preState => (
                {
                    watermarkProps: { ...preState.watermarkProps, [name]: !preState.watermarkProps[name] }
                }), () => this.handleTextChange(name))
        } else if (name === "fill") { //for color
            this.setState(preState => (
                {
                    watermarkProps: { ...preState.watermarkProps, [name]: value }
                }), () => this.handleTextChange(name))
        }
    }
    handleFontFamily(e) {//for font family
        this.setState(preState => (
            { watermarkProps: { ...preState.watermarkProps, fontFamily: e.target.value } }), () => { this.handleTextChange("fontFamily") });
    }

    handleOpacity(e, val) { //Opacity needs specific callback function
        let name = e.currentTarget.name;
        this.setState(preState => (
            { watermarkProps: { ...preState.watermarkProps, opacity: val / 100 } }), () => {
                console.log(this.state.watermarkProps.opacity);
                this.handleTextChange("opacity");
            });
    }


    delCanvas() { //reset canvas for wrong image
        let canvas = this.props.canvas;
        canvas.clear();
        canvas.dispose();
        this.setState({ watermark: {} })
        canvas = new fabric.Canvas('canvas', {
            preserveObjectStacking: true,
            fireRightClick: true,
            allowTouchScrolling: true
        })
        this.props.resetCanvas(canvas);
        canvas.on('mouse:wheel', function (opt) { //listen to mouse wheel event, zooming
            var delta = opt.e.deltaY;
            var zoom = canvas.getZoom();
            zoom *= 0.999 ** delta;
            if (zoom > 20) zoom = 20;
            if (zoom < 0.01) zoom = 0.01;
            canvas.zoomToPoint({ x: opt.e.offsetX, y: opt.e.offsetY }, zoom);
            opt.e.preventDefault();
            opt.e.stopPropagation();
        });
        canvas.renderAll();
        console.log(canvas);
    }

    handleTextChange(stylename, text) {// function for changing watermark according to given props
        let canvas = this.props.canvas;
        console.log(stylename);
        if (this.state.watermark instanceof fabric.Object) {
            if (stylename === "text") { //for the text value change
                this.state.watermark.set({
                    [stylename]: text
                })

            } else {
                this.state.watermark.set({ //for other configurations
                    [stylename]: this.state.watermarkProps[stylename]
                })
            }
            console.log(this.state.watermark);
            canvas.renderAll(); //refresh
        }
    }
    toggleDrawer(open, event) { //for event of download menu , close and open
        if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        this.setState({ drawer: open });
    }

    handleText() {
        let canvas = this.props.canvas;
        let getObj = canvas.getObjects();
        let img = getObj[0];
        console.log(canvas);
        console.log(this.state.watermark);
        console.log(img);
        if (img && Object.entries(this.state.watermark).length === 0) { //if no text object yet, create one and add to canvas 
            let imgSize = img.height*img.height;
            let textSize = imgSize/15000 // rough scale of watermark size 
            //console.log( imgSize,textSize)
            let text = new fabric.Text(this.state.input, {
                fontSize: textSize,
                left: img.left,
                top: img.top,
                id: 'text',
                fontStyle: this.state.watermarkProps.fontStyle,
                fontWeight: this.state.watermarkProps.fontWeight,
                fill: this.state.watermarkProps.fill,
                fontFamily: this.state.watermarkProps.fontFamily,
                underline: this.state.watermarkProps.underline,
                opacity: this.state.watermarkProps.opacity,
                globalCompositeOperation: 'source-atop'
            });
            canvas.add(text);
            this.setState({ watermark: text }, () => { console.log(this.state.watermark) });
            canvas.discardActiveObject();
        } else {
            this.handleTextChange("text", this.state.input); //change text value of existing text object 
        }
        canvas.renderAll();
    }

    batchDownload() {
        this.props.saveCurrentcanvas(); //save current editing image
        let Dummycanvas = new fabric.Canvas('canvas', { // a canvas object for rendering
            id: "dummy",
            preserveObjectStacking: true,
            fireRightClick: true,
            allowTouchScrolling: true
        })
        //console.log(Dummycanvas)
        /* this.props.ArrayOfCanvas.forEach(canvas => console.log(canvas));  */
        let counter = 0;
        /* this.props.ArrayOfCanvas.forEach((canvas,index) => {
             this.convertToImagen(Dummycanvas, canvas,index,counter);
         });*/
        this.convertToImagen(Dummycanvas, counter);
    }

    convertToImagen(canvas, index) { //convert canvas one by one to image recursively, race condition cause error
        // let canvas = this.props.canvas;
        console.log(this.state.outputProps); //for testing
        let cobj = this.props.ArrayOfCanvas[index];
        canvas.loadFromJSON(cobj, () => {
            const sel = new fabric.Group(canvas.getObjects(), { //a group to wrap everything in canvas
            })
            let url
            if (this.state.outputProps.format === "png") {
                url = sel.toDataURL('png').replace("image/png", "image/octet-stream"); //make the url vaild for downloading
            } else if (this.state.outputProps.format === "jpeg") {
                url = sel.toDataURL('jpeg').replace("image/jpeg", "image/octet-stream"); //make the url vaild for downloading
            }
            let filename = this.state.outputProps.filename;
            if (this.state.outputProps.format === "png") {//for png 
                if (this.state.outputProps.numTag) { //filename number tag option 
                    filename += `-${index}`
                }
                this.saveAs(url, filename.concat(".png")); //download
            } else if (this.state.outputProps.format === "jpeg") { //for jpeg
                if (this.state.outputProps.numTag) { //filename number tag option 
                    filename += `-${index}`
                }
                this.saveAs(url, filename.concat(".jpeg")); //download
            }
            let items = sel.getObjects();
            sel.destroy(); //delete the group after finished rendering
            canvas.remove(sel);
            items.forEach(function (item) {
                item.set('dirty', true); //enable render objects' cache
            })
            canvas.add.apply(canvas, items);
            canvas.renderAll(); //refresh canvas
            console.log(index);
            if (index === this.props.ArrayOfCanvas.length - 1) { //after last image, delete the canvas for rendering
                console.log("finsih");
                canvas.clear();
                canvas.dispose();
                this.props.canvas.renderAll();
                return;
            }
            index++;
            this.convertToImagen(canvas, index); //render image recursively, sync
        });

    }

    saveAs(uri, filename) { //simulate user clicking to perform automatic download 
        var link = document.createElement('a');

        if (typeof link.download === 'string') {

            link.href = uri;
            link.download = filename;

            //Firefox requires the link to be in the body
            document.body.appendChild(link);

            //simulate click
            link.click();

            //remove the link when done
            document.body.removeChild(link);

        } else {
            window.open(uri);
        }
    }

    componentDidUpdate(prevProps) {  //React method, execute if props.canvas changed
        if (prevProps.canvasSpin !== this.props.canvasSpin) { //spinning signal indicate change 
            let obj = this.props.canvas.getObjects();
            console.log(obj);
            if (obj.length === 0) { //reset control buttons to default for empty canvas 
               /* console.log("NewCanvas")
                console.log(this.props.canvas);*/
                this.setState({ watermark: {} });
                this.setState({ input: "" });
                this.setState({ watermarkProps: deft });
                this.setState({ repeat: deftR });
            } else {
                let watermark = obj[1];
                let group = obj[2];
               // console.log(group);
                if (watermark !== undefined) { //change the control buttons according to current watermark
                    this.setState({ watermark: watermark });
                    this.setState({ input: watermark.text });
                    for (const prop in this.state.watermarkProps) {
                        this.setState(preState => ({ watermarkProps: { ...preState.watermarkProps, [prop]: watermark[prop] } }))
                    }
                    if (group !== undefined) { //change the pattern section of control buttons according to pattern
                        let canvas = this.props.canvas;
                        if (group.height > canvas.height) {
                            this.setState({ repeat: { x: false, y: true } })
                        } else if (group.width > canvas.width) {
                            this.setState({ repeat: { x: true, y: false } })
                        }
                    }else{
                        this.setState({ repeat: deftR });
                    }
                } else { //has image but no watermark, still set to default
                    this.setState({ watermark: {} }); //reset control buttons
                    this.setState({ input: "" });
                    this.setState({ watermarkProps: deft }); //default water attributes
                    this.setState({ repeat: deftR });  //default pattern attributes
                }
                console.log("loadoldcanvas")
            }
        }
    }




    //layout of bottom buttons
    render() {
        return (
            <Grid container direction="row" justify="center" spacing={3}>
                <Grid item>
                    <FormControlLabel
                        control={
                            <Checkbox
                                color="primary"
                                checked={this.state.repeat.x}
                                name="x"
                                value={this.state.repeat.x}
                                inputProps={{ 'aria-label': 'secondary checkbox' }}
                                disabled={this.state.repeat.y}
                                onChange={(e) => this.handleRepeat(e)}
                            />
                        }
                        label="repeat-x"
                        labelPlacement="End"
                    />
                    <FormControlLabel
                        control={
                            <Checkbox
                                color="primary"
                                checked={this.state.repeat.y}
                                name="y"
                                value={this.state.repeat.y}
                                inputProps={{ 'aria-label': 'secondary checkbox' }}
                                disabled={this.state.repeat.x}
                                onChange={(e) => this.handleRepeat(e)}
                            />
                        }
                        label="repeat-y"
                        labelPlacement="End"
                    />
                </Grid>
                <Grid item>
                    <FormControl>
                        <InputLabel htmlFor="filled-age-native-simple">FontFamily</InputLabel>
                        <Select
                            native
                            defaultValue="Times New Roman"
                            disabled={this.state.repeat.y || this.state.repeat.x}
                            onChange={(e) => { this.handleFontFamily(e) }}
                            value={this.state.watermarkProps.fontFamily}
                        >
                            <option value={"Arial"}>Arial</option>
                            <option value={"Times New Roman"}>Times New Roman</option>
                            <option value={"Courier New"}>Courier New</option>
                            <option value={"Brush Script MT"}>Brush Script MT</option>
                            <option value={"Trebuchet MS"}>Trebuchet MS</option>
                        </Select>
                    </FormControl>
                </Grid>
                <Grid item> <TextField id="standard-basic" disabled={this.state.repeat.y || this.state.repeat.x} label="watermark" inputProps={{ maxLength: 45 }}
                    onChange={(e) => this.handleInput(e.target.value)} value={this.state.input} /></Grid>
                <Grid item>
                    <ButtonGroup variant="contained" color="primary" aria-label="contained primary button group">
                        <Button onClick={this.handleText} disabled={this.state.repeat.y || this.state.repeat.x}>
                            apply
                        </Button>
                        <Button onClick={this.delCanvas}>
                            reset
                         </Button>
                    </ButtonGroup>
                    <ButtonGroup color="primary" aria-label="contained primary button group" className={controlCSS.textbuttons} disabled={this.state.repeat.y || this.state.repeat.x}>

                        <IconButton aria-label="Bold" value="bold" name="fontWeight" color={this.state.watermarkProps.fontWeight === "bold" ? "secondary" : "primary"}
                            onClick={(e) => { this.handleProps(e) }}>
                            <FormatBold fontSize="small" />
                        </IconButton>
                        <IconButton aria-label="italic" value="italic" name="fontStyle" color={this.state.watermarkProps.fontStyle === "italic" ? "secondary" : "primary"}
                            onClick={(e) => { this.handleProps(e) }}>
                            <FormatItalic fontSize="small" />
                        </IconButton>
                        <IconButton aria-label="underlined" name="underline" color={this.state.watermarkProps.underline ? "secondary" : "primary"}
                            onClick={(e) => { this.handleProps(e) }}>
                            <FormatUnderlined fontSize="small" />
                        </IconButton>

                        <Grid item>
                            <input type="color" id="favcolor" name="fill" style={{ marginTop: 15 }}
                                value={this.state.watermarkProps.fill} onChange={(e) => { this.handleProps(e) }}
                                disabled={this.state.repeat.y || this.state.repeat.x}
                            />
                        </Grid>
                        <Grid item className={controlCSS.slider}>
                            <Typography id="continuous-slider" align="center" color="textPrimary" >
                                Opacity
                            </Typography>

                            <Slider
                                style={{ width: 120, left: 15, marginBottom: 10 }} aria-labelledby="continuous-slider" name="opacity"
                                value={Math.round(this.state.watermarkProps.opacity * 100)}
                                onChange={this.handleOpacity} disabled={this.state.repeat.y || this.state.repeat.x} valueLabelDisplay="auto"

                            />
                        </Grid>
                    </ButtonGroup>
                    <Grid item justify="center">
                        <Button
                            variant="contained"
                            color="default"
                            startIcon={<CloudDownload />}
                            fullWidth
                            component="label"
                            onClick={(e) => this.toggleDrawer(true, e)}
                        >
                            download menu
                    </Button>
                        <Drawer anchor="right" open={this.state.drawer} onClose={(e) => this.toggleDrawer(false, e)}>
                            <TextField id="standard-basic" label="filename" name="filename" inputProps={{ maxLength: 45 }} onChange={(e) => this.handleOutputProps(e)} />
                            <FormControl component="fieldset">
                                <center>
                                    <FormControlLabel
                                        value="start"
                                        control={
                                            <Checkbox
                                                color="primary"
                                                checked={this.state.outputProps.numTag}
                                                name="numTag"
                                                value={this.state.outputProps.numTag}
                                                inputProps={{ 'aria-label': 'secondary checkbox' }}
                                                onChange={(e) => this.handleOutputProps(e)}
                                            />
                                        }
                                        label="Number Tag"
                                        labelPlacement="start"
                                    />
                                    <FormLabel component="legend">Format</FormLabel>
                                    <RadioGroup aria-label="gender" name="Format">
                                        <center>
                                            <FormControlLabel name="format" value="png" control={<Radio />} label="PNG" onChange={(e) => this.handleOutputProps(e)} />
                                            <FormControlLabel name="format" value="jpeg" control={<Radio />} label="JPEG" onChange={(e) => this.handleOutputProps(e)} />
                                        </center>
                                    </RadioGroup>
                                </center>
                            </FormControl>
                            <Button
                                color="default"
                                startIcon={<CloudDownload />}
                                fullWidth
                                component="label"
                                download="myImage.jpg"
                                href=""
                                onClick={this.batchDownload}
                            >
                                download File
                           </Button>
                        </Drawer>
                    </Grid>

                </Grid>
            </Grid>
        )
    }
}

export default Control;