import React, {Component} from 'react';
import Button from '@material-ui/core/Button';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import Grid from '@material-ui/core/Grid';
import uploadBtnCSS from './uploadButton.module.css';
import IconButton from '@material-ui/core/IconButton';
import {ArrowBackIos,ArrowForwardIos} from '@material-ui/icons';
import { fabric } from "fabric";


class UploadButton extends Component{
    constructor(){
        super();
        this.state ={
          canvas:{},
        } 
    }

  
    render(){ //view
        return(
      <Grid container direction="row" justify="center" spacing={3}>
          <Grid item xs={2} md={1} className={uploadBtnCSS.center}>
              <IconButton aria-label="ArrowBack" value="back" onClick={(e) => this.props.changeCanvas(e)}>
                  <ArrowBackIos fontSize="small"/>
              </IconButton>
           </Grid>
           <Grid item xs={2} md={1} className={uploadBtnCSS.center}>
              <IconButton aria-label="ArrowFront" value="front"  onClick={(e) => this.props.changeCanvas(e)}>
                  <ArrowForwardIos fontSize="small"/>
              </IconButton>
             </Grid>

         <Grid item xs={4} md={3} className={uploadBtnCSS.center}>
            <Button
             variant="contained"
              color="default"
             startIcon={<AddPhotoAlternateIcon/>}
             fullWidth
             component="label"
             onClick ={this.props.newCanvas}
            >
              New Canvas
          </Button>
          
           </Grid>
         <Grid item xs={4} md={3} className={uploadBtnCSS.center}>
            <Button
             variant="contained"
              color="default"
             startIcon={<CloudUploadIcon />}
             fullWidth
             component="label"
            >
              Upload File
              <input
                type="file"
                hidden
                id="imageInput"
                accept="image/*"
                onChange={e => this.props.uploadImage(e)}
              />
          </Button>
           </Grid>
       </Grid>
          
      )
    }
}

export default UploadButton;