import React, { Component } from 'react';
import logo from './logo.svg';
import MainCSS from './App.module.css';
import Container from '@material-ui/core/Container';
import Header from './components/header.js';
import Grid from '@material-ui/core/Grid';
import UploadButton from './components/UploadButton';
import Canvas from './components/canvas';
import Control from './components/controlpanel';
import { fabric } from "fabric";

class App extends Component {
  constructor() {
    super();
    this.state = {  //set of states to save state of different parts
      images: [],
      canvas: {},
      ArrayOfCanvas: [],
      canvasIndex: 0,
      canvasSpin: false
    }
    this.uploadImage = this.uploadImage.bind(this);  //bind to this class for child component to modify parent's states
    this.resizeCanvas = this.resizeCanvas.bind(this);
    this.startPan = this.startPan.bind(this);
    this.newCanvas = this.newCanvas.bind(this);
    this.resetCanvas = this.resetCanvas.bind(this);
    this.changeCanvas = this.changeCanvas.bind(this);
    this.saveCurrentcanvas = this.saveCurrentcanvas.bind(this);
  }

  uploadImage(e) {
    let canvas = this.state.canvas;
    if (canvas.getObjects().length === 0) { //if canvas is empty
      var reader = new FileReader();   //to read file
      reader.onload = function (event) {
        var imgObj = new Image();  
        imgObj.src = event.target.result;

        imgObj.onload = () => {
          // start fabricJS stuff

          var image = new fabric.Image(imgObj);  //new fabric.Image instance
          image.set({
            padding: 10,
            cornersize: 10,
            hasControls: false,
            selectable: false,
            lockMovementX: true,
            lockMovementY: true
          });
          canvas.add(image);  //add to the current fabric.canvas 
          console.log(image) //for testing
          canvas.setActiveObject(image);
          let w = document.getElementsByClassName("canvas-container")[0].offsetWidth; 
          let h = document.getElementsByClassName("canvas-container")[0].offsetHeight;
          /*image.scaleToWidth(w-200);*/
          /* image.scaleToHeight(h-200);*/
          canvas.centerObject(image); //center the image 
          // end fabricJS stuff

        }
      }
      if (e.target.files[0]) {
        console.log(e.target.files[0]) //for testing 
        reader.readAsDataURL(e.target.files[0]); //read the image data url 
      }
    }

    this.setState({ isload: true }); //indicate loaded but not using anymore
    document.getElementById("imageInput").value = null; //clean input value
  }

  resetCanvas(newCanvas) {  //function to reset canvas after swapped or created new canvas 
    this.setState({ canvas: newCanvas }, () => {
      this.setState(prev => ({ canvasSpin: !prev.canvasSpin }));
      console.log(this.state.ArrayOfCanvas);
      console.log(this.state.canvasIndex);
      console.log(this.state.canvas);
    })
  }

  saveCurrentcanvas() { //save the state of current canvas
    let arr = this.state.ArrayOfCanvas
    arr.splice(this.state.canvasIndex, 1, fabric.util.object.clone(this.state.canvas.toObject(['selectable', 'hasControls', 'lockMovementX', ' lockMovementY'])))
    this.setState({
      ArrayOfCanvas: arr
    })
  }


  changeCanvas(e) { //to change canvas to other in ArrayOfCanvas
    let canvas = this.state.canvas;
    let value = e.currentTarget.value;
   /* console.log(this.state.ArrayOfCanvas);
    console.log(this.state.canvas);*/
    if (value === "back") { //go to previous
      if (this.state.canvasIndex > 0) { //if current is not the first one 
        let arr = this.state.ArrayOfCanvas
        arr.splice(this.state.canvasIndex, 1, fabric.util.object.clone(this.state.canvas.toObject(['selectable', 'hasControls', 'lockMovementX', ' lockMovementY'])))
        this.setState({  //save the current canvas
          ArrayOfCanvas: arr
        })
        this.setState(preState => ({ canvasIndex: preState.canvasIndex - 1 }), () => {
          canvas.loadFromJSON(this.state.ArrayOfCanvas[this.state.canvasIndex], () => {  //load canvas from array
            this.resetCanvas(canvas);
          })
        })
      }
    } else if (value === "front") { //go to next
      if (this.state.canvasIndex < this.state.ArrayOfCanvas.length - 1) { //if current is not the last one 
        let arr = this.state.ArrayOfCanvas
        arr.splice(this.state.canvasIndex, 1, fabric.util.object.clone(this.state.canvas.toObject(['selectable', 'hasControls', 'lockMovementX', ' lockMovementY'])))
        this.setState({
          ArrayOfCanvas: arr //save the current canvas
        })
        this.setState(preState => ({ canvasIndex: preState.canvasIndex + 1 }), () => {
          canvas.loadFromJSON(this.state.ArrayOfCanvas[this.state.canvasIndex], () => {
            this.resetCanvas(canvas); //load canvas from array
          })
        })
      }
    }
    canvas.renderAll(); //refresh canvas
  }

  newCanvas() { //create new canvas
    if (this.state.ArrayOfCanvas.length < 5) { //if under maximum number of canvas
      let oldcanvas = this.state.canvas;
      let arr = this.state.ArrayOfCanvas
      arr.splice(this.state.canvasIndex, 1, fabric.util.object.clone(oldcanvas.toObject(['selectable', 'hasControls', 'lockMovementX', ' lockMovementY'])))
      this.setState({
        ArrayOfCanvas: arr   //save current canvas
      })
      oldcanvas.clear();  //clean old canvas from html
      oldcanvas.dispose();
      let canvas = new fabric.Canvas('canvas', {  //new fabric.canvas
        preserveObjectStacking: true,
        fireRightClick: true,
        allowTouchScrolling: true
      })
      canvas.on('mouse:wheel', function (opt) { //new canvas listen to mouse wheel event, zooming
        var delta = opt.e.deltaY;
        var zoom = canvas.getZoom();
        zoom *= 0.999 ** delta;
        if (zoom > 20) zoom = 20;
        if (zoom < 0.01) zoom = 0.01;
        canvas.zoomToPoint({ x: opt.e.offsetX, y: opt.e.offsetY }, zoom); //zoom to mouse point
        opt.e.preventDefault();
        opt.e.stopPropagation();
      });
      this.setState(preState => ({ canvasIndex: this.state.ArrayOfCanvas.length }))
      this.resetCanvas(canvas);
      canvas.renderAll();
    }
  }

  startPan(event) {//panning, listen to right click that user can drag the canvas  
    let canvas = this.state.canvas;
    if (event.button !== 2) {
      return;
    }
    var x0 = event.screenX,
      y0 = event.screenY;
    function continuePan(event) {
      var x = event.screenX,
        y = event.screenY;
      canvas.relativePan({ x: x - x0, y: y - y0 });
      x0 = x;
      y0 = y;
    }
    function stopPan(event) {
      window.removeEventListener("mousemove", continuePan);
      window.removeEventListener("mouseup", stopPan);
    };
    window.addEventListener("mousemove", continuePan);
    window.addEventListener("mouseup", stopPan);

  }



  resizeCanvas() {//set canvas to match size of html container
    let canvas = this.state.canvas;
    canvas.setHeight(document.getElementById('container').offsetHeight);
    canvas.setWidth(document.getElementById('container').offsetWidth);
    canvas.renderAll();
  }

  componentDidMount() { //React lifecycle similar to window.onload
    let canvas = new fabric.Canvas('canvas', { //new fabric canvas
      preserveObjectStacking: true,
      fireRightClick: true,
      allowTouchScrolling: true
    })
   // console.log(canvas);    //for testing
    this.setState({ canvas: canvas }, () => {  //save it to state
      this.resizeCanvas();     
    });
    canvas.on('mouse:wheel', function (opt) { //listen to mouse wheel event, zooming
      var delta = opt.e.deltaY;
      var zoom = canvas.getZoom();
      zoom *= 0.999 ** delta;
      if (zoom > 20) zoom = 20;
      if (zoom < 0.01) zoom = 0.01;
      canvas.zoomToPoint({ x: opt.e.offsetX, y: opt.e.offsetY }, zoom);
      opt.e.preventDefault();
      opt.e.stopPropagation();
    });

    let container = document.getElementById("container");
    container.addEventListener("mousedown", this.startPan);
    container.addEventListener('contextmenu', e => {  //prevent contextmenu popup in canvas
      e.preventDefault();
    });
  }


  render() { //view
    return (
      <div className={MainCSS.container}>
        <Header />
        <Grid container spacing={1} justify="center" md={10} sm={12} style={{ margin: 'auto' }}>
          <UploadButton uploadImage={this.uploadImage} newCanvas={this.newCanvas}
            ArrayOfCanvas={this.state.ArrayOfCanvas} resetCanvas={this.resetCanvas} canvas={this.state.canvas} canvasIndex={this.state.canvasIndex} changeCanvas={this.changeCanvas} />
          <Canvas images={this.state.images} isload={this.state.isload} canvas={this.state.canvas} />
          <Control canvas={this.state.canvas} resetCanvas={this.resetCanvas.bind(this)} canvasSpin={this.state.canvasSpin}
            ArrayOfCanvas={this.state.ArrayOfCanvas} saveCurrentcanvas={this.saveCurrentcanvas} />
        </Grid>
      </div>
    );
  }
}

export default App;
